import React, { Component } from 'react'
import { AppBar, Toolbar, Typography, IconButton, Button, Menu, MenuItem} from '@material-ui/core';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import GitHubIcon from '@material-ui/icons/GitHub';
import PopupState, { bindTrigger, bindMenu } from 'material-ui-popup-state';

export default class MyNavBar extends Component {
    
    handleDropdownClick = (e,popupState) => {
        popupState.close()
        this.props.onChange(e.target.id)
        
    }
    render() {
        return (
            <AppBar position="fixed">
                <Toolbar>
                    <Typography color="inherit" style={{ flex: 1 }}>
                        Dylan TerMolen's Portfolio | Currently a Work in Progress
                    </Typography>
                    <span>
                    <PopupState variant="popover" popupId="demo-popup-menu">
                        {popupState => (
                            <React.Fragment>
                                <Button variant="contained" color="primary" {...bindTrigger(popupState)}>
                                    See My Projects
                                </Button>
                                <Menu {...bindMenu(popupState)} >
                                    <MenuItem id="gan" onClick={e => this.handleDropdownClick(e,popupState)}>Crest GAN</MenuItem>
                                    <MenuItem id="twfc" onClick={e => this.handleDropdownClick(e,popupState)}>Twitter FC</MenuItem>
                                    <MenuItem id="crossword" onClick={e => this.handleDropdownClick(e,popupState)}>Crossword Interface</MenuItem>
                                </Menu>
                            </React.Fragment>
                        )}
                    </PopupState>
                        <IconButton href="https://www.linkedin.com/in/dylan-termolen-748048170/" color="inherit" aria-label="Save">
                            <LinkedInIcon />
                        </IconButton>
                        <IconButton href="https://github.com/Dylan-TerMolen" color="inherit" aria-label="More Options">
                            <GitHubIcon />
                        </IconButton>
                    </span>
                </Toolbar>
            </AppBar>
        )
    }
}
