import React, { Component } from 'react'
import MyNavBar from './MyNavBar'
// import Board from './CrosswordComponents/Board'
import CrestGAN from './CrestGAN'

export default class Portfolio extends Component {
    constructor(props){
        super(props);
        this.state = {project:'gan'}
    }

    handleProjectChange = (newProject) => {
        this.setState({project:newProject})
    }
    render() {
        const divStyle = {
            padding: "50px 50px 5px 50px"
          };
        var project;
        switch(this.state.project){
            case 'gan':
                project = <CrestGAN/>
                break;
            case 'crossword':
                project = <h1> COMING SOON (Crossword Interface)</h1> 
                break;
            case 'twfc':
                project = <h1> COMING SOON (Twitter FC)</h1>
                break;
            default:
                project = <h1>Should not be seeing this</h1>
        }


        return (
            <div>
                <MyNavBar onChange={this.handleProjectChange}/> 
                <div style={divStyle}>            
                    {project}
                </div>
            </div>
        )
    }
}
